@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
	height: 100vh;
	background: rgb(204,146,255);
	background: linear-gradient(0deg, rgba(204,146,255,1) 0%, rgba(255,51,163,0.4164040616246498) 100%);
	text-align: center;
}

body *{
	font-family: 'Permanent Marker', cursive;
}

h1{
	padding-top: 50px;
	color: white;
	text-align: center;
	font-size: 70px;
}

#play_new_game{
	background: transparent;
	border: 5px double white;
	font-size: 30px;
	color: white;
	padding: 10px 20px;
	margin-top: 30px;
	cursor: pointer;
}


/*
*
*	DRAW LIFE
*
*/
#life{
	margin-top: 30px;
	text-align: center;
	margin-bottom: 50px;
}
#life .heart.empty{
	opacity: 0.3;
}

.heart {
	background-color: white;
	display: inline-block;
	height: 20px;
	margin: 0 10px;
	position: relative;
	top: 0;
	transform: rotate(-45deg);
	width: 20px;
}

.heart:before,
.heart:after {
	content: "";
	background-color: white;
	border-radius: 50%;
	height: 20px;
	position: absolute;
	width: 20px;
}

.heart:before {
	top: -10px;
	left: 0;
}

.heart:after {
	left: 10px;
	top: 0;
}

/*
*
*	END GAME MESSAGE
*
*/
#win_message{
	font-size: 40px;
	color: white;
	padding: 30px;
}
#lost_message{
	font-size: 40px;
	color: black;
	padding: 30px;
}

/*
*
*	WORD TO FIND
*
*/
#wrapper_new_word{
	border: 1px solid transparent;
	width: auto;
	text-align: center;
	margin: 0 auto;
}

#wrapper_new_word span{
	display: inline-block;
	border-bottom: 3px solid white;
	font-size: 40px;
	padding: 5px 20px;
	color: white;
	margin: 0 5px;
}

#wrapper_new_word span.notfinded{
	color: rgba(255,255,255,0.3);
}

#wrapper_new_word span.lost{
	color: black;
}

#wrapper_new_word::after{
	clear: both;
	content: " ";
	min-height: 1px;
	display:block;
}

/*
*
*	KEYBOARD PART
*
*/
#keyboard{
	border: 1px solid transparent;
	width: 90%;
	max-width: 600px;
	margin: 0 auto;
	text-align: center;
	margin-top: 30px;
}

#keyboard button{
	color: white;
	background: transparent;
	border: none;
	font-size: 40px;
	padding: 5px 20px;
	cursor:pointer;
}

#keyboard .used{
	opacity: 0.3;
}
